.consumer_expenditure {
  font-family: Arial, sans-serif;
  line-height: 1.6;
  color: #333;
  font-size: 14px;

  .report_section {
    h2 {
      font-size: 24px;
      font-weight: bold;
    }
  }
  .report_list {
    margin: 20px;
    margin-top: 20px;
  }

  .report_description {
    margin-top: 20px;
  }
}

.report_two_plots {
  display: flex;
}

.visualization {
  &_block {
    display: flex;
  }

  &_desc {
    flex: 0.5;
    margin-right: 40px;
    margin-top: 20px;
  }

  &_placeholder {
    flex: 1;
  }
}