.world-food {
  min-height: 80vh;
  max-width: 1000px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  line-height: 150%;
  font-size: 14px;

  &_navigation {
    display: flex;
    justify-content: space-between;
    overflow: auto;
    white-space: nowrap;
    scrollbar-width: none;

    div {
      margin-top: 10px;
    }
  }
}

h1 {
  margin-top: 20px;
}

@media (min-width: 0) and (max-width: 700px) {
  .world-food_navigation {

    .nav_item {
      margin: 20px 0;
      margin-right: 50px;
    }
  }

  .visualization_block {
    display: flex;
    flex-direction: column;
  }
}

.nav_item {
  font-size: 1rem;
  position: relative;
  padding-bottom: 2px;
  transition: transform 0.5s ease;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  margin: 20px;
  margin-right: 80px;
  border-bottom: 1px solid black;
  white-space: nowrap;
  user-select: none;

  &.nav_active {
    transform: scale(1.1);
    color: #ffc000;
    border-bottom: 1px solid #ffc000;

    font-weight: bold;

    &::after {
      width: 100%;
    }

    &:hover::after {
      width: 100%;
    }
  }

  &:hover {
    transform: scale(1.1);

    &::after {
      width: 100%;
    }
  }
}

.report_text {
  margin-top: 20px;
  font-size: 14px;
  line-height: 150%;
}

h2 {
  font-size: 24px;
  font-weight: bold;
}

.report_title {
  cursor: pointer;
  font-size: 24px;
  margin-top: 20px;
  line-height: 150%;
  user-select: none;
}

.report_list {
  margin: 20px;
  margin-top: 20px;

  li {
    line-height: 150%;
    font-size: 14px;
    padding-left: 10px;
  }
}

.report_description {
  margin-top: 20px;

  h4 {
    font-size: 16px;
  }
  p {
    margin: 10px 0;
  }
}

.report_two_plots {
  display: flex;
}

.visualization {
  &_block {
    display: flex;
  }

  &_desc {
    flex: 0.5;
    margin-right: 40px;
    margin-top: 20px;
  }

  &_placeholder {
    flex: 1;
  }
}

.predictions-section,
.recommendations-section {
  h4 {
    margin-top: 30px;
    font-size: 16px;
  }
}


.navigation-arrows {
  display: flex;
  margin-top: 20px;
  justify-content: space-between;

  &-end {
    justify-content: flex-end;
  }

  button {
    margin-top: 30px;
    background-color: #ffc000;
    color: rgb(0, 0, 0);
    font-weight: bold;
    border: none;
    padding: 8px 16px;
    margin: 0 8px;
    cursor: pointer;
    outline: none;
    transition: background-color 0.3s ease;

    &:hover {
      background-color: #ffc000;
    }

    &:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }

  }
}